import React, {Component} from 'react';
import Button from './components/Button';
import './css/style.css';

class App extends Component {
  constructor(props){
    super(props);
    // this.ref = React.createRef();
    this.state = {
      result: 0,
      operator: '',
      a : 0,
      b: '',
      flag: false,
      msg: ''
    };
  }

  reset = ()=>{
    this.setState({a:0,b:'', result:0,flag:false, msg:'', operator: '' });
  }

  calculate = (symbol)=>{
    let {a,b,operator, result} = this.state;
    console.log("befor b = ",b);
    b = (b==='')? a:b;
    // console.log("after b = ",b);

    result = operate(a,operator,b);
    this.setState({msg:`${a} ${operator} ${b} = `,result,b, a:result, flag: false})
  }

  addDigit = (digit)=>{
    // console.log(digit);
      let {a,b, flag} = this.state;
      if(flag){
        b= (b==='')? digit :b*10+digit;
        this.setState({b, result:`${b}`});
        // console.log("b = "+ b);
      }else{
        a=a*10+digit;
        this.setState({a, result:`${a}`});
      }
  }
  action = (operator)=>{
    let {a,b,result,flag} = this.state;
    // console.log(operator);
    if(b !== '' && flag){
      result = operate(a,this.state.operator, b);
      a= result;
      this.setState({msg:`${a} ${operator}`, a,b:'',result,operator,});
    }else{
      this.setState({msg: `${a} ${operator}`,a,b:'',result:a,operator, flag: true});
    }
  }
  keyprssHandler = (e)=> {
    // document.addEventListener('keypress',(e)=>{
      let name = e.key;
      // let code = e.code;
      if(name ==='Enter')
        name = '=';
      // console.log('key ',name ,' pressed');
      let btn = document.getElementById(name);
      if(btn)
        btn.click();
      // console.log(btn);
    // })
  }
  componentWillUpdate(){
    console.log("clear");
  }
  componentDidMount(){
    document.addEventListener('keypress', this.keyprssHandler);
  }

  

  render(){
    const buttons = [
      {symbol: 'C',  cols:3, action:this.reset},
      {symbol: '/',  cols:1, action: this.action},
      {symbol: 7,  cols:1, action: this.addDigit},
      {symbol: 8,  cols:1, action: this.addDigit},
      {symbol: 9,  cols:1, action: this.addDigit},
      {symbol: '*',  cols:1, action: this.action},
      {symbol: 4,  cols:1, action: this.addDigit},
      {symbol: 5,  cols:1, action: this.addDigit},
      {symbol: 6,  cols:1, action: this.addDigit},
      {symbol: '-',  cols:1, action: this.action},
      {symbol: 1,  cols:1, action: this.addDigit},
      {symbol: 2,  cols:1, action: this.addDigit},
      {symbol: 3,  cols:1, action: this.addDigit},
      {symbol: '+',  cols:1, action: this.action},
      {symbol: 0,  cols:2, action: this.addDigit},
      {symbol: '.',  cols:1, action: this.addDigit},
      {symbol: '=',  cols:1, action: this.calculate},
    ]
    return (
      <div className="App">
        <div className="box">
          {this.state.msg !== '' ?
            <div className="floaty-last">{`${this.state.msg} `}</div>
          
          :null}
          <input className="result" type="text" value={this.state.result} placeholder={'0'} readOnly></input>
          {buttons.map((btn,i)=>{
            return <Button  key={i} symbol={btn.symbol} cols={btn.cols} action={btn.action} ></Button>
          })}
        </div>
      


      </div>
    );
  }
}
function operate(a,operator,b) {
  console.log(a,operator,b);
  switch(operator){
    case '+':
      return a+b;
    case '-':
      return a-b;
    case '*':
      return a*b;
    case '/':
      return (b!==0)? a/b: 'Error';
    default:
      return 0;
  }
}



export default App;
